import IndexPage from '@views/imageList'
import BucketsManage from '@views/bucketsManage'

export default [
  {
    path: '/',
    name: '',
    component: IndexPage,
    meta:{
      title: 'qim-'
    }
  }, {
    path: '/buckets',
    name: 'Bucket',
    component: BucketsManage,
    meta:{
      keepAlive: true,
      title: 'qim-Bucket'
    }
  }
]



